var _objectSpread = require("/Users/apple/Documents/Home/Practice/appointment-booking/node_modules/@babel/runtime/helpers/objectSpread");

/**
 * The result of dump.js is a big JSON tree
 * which can be easily serialized (for instance
 * to be sent from a webworker to the main app
 * or through Node's IPC), but we want
 * a (circular) DOM-like interface for walking
 * through the data.
 *
 * @fileoverview DOM-like interface for walking through data
 * @author Kevin Kwok <antimatter15@gmail.com>
 * @author Guillermo Webster <gui@mit.edu>
 * @author Jerome Wu <jeromewus@gmail.com>
 */
module.exports = function (iPage) {
  var page = _objectSpread({}, iPage, {
    paragraphs: [],
    lines: [],
    words: [],
    symbols: []
  });

  page.blocks.forEach(function (iBlock) {
    var block = _objectSpread({}, iBlock, {
      page: page,
      lines: [],
      words: [],
      symbols: []
    });

    block.paragraphs.forEach(function (iPara) {
      var para = _objectSpread({}, iPara, {
        block: block,
        page: page,
        words: [],
        symbols: []
      });

      para.lines.forEach(function (iLine) {
        var line = _objectSpread({}, iLine, {
          paragraph: para,
          block: block,
          page: page,
          symbols: []
        });

        line.words.forEach(function (iWord) {
          var word = _objectSpread({}, iWord, {
            line: line,
            paragraph: para,
            block: block,
            page: page
          });

          word.symbols.forEach(function (iSym) {
            var sym = _objectSpread({}, iSym, {
              word: word,
              line: line,
              paragraph: para,
              block: block,
              page: page
            });

            sym.line.symbols.push(sym);
            sym.paragraph.symbols.push(sym);
            sym.block.symbols.push(sym);
            sym.page.symbols.push(sym);
          });
          word.paragraph.words.push(word);
          word.block.words.push(word);
          word.page.words.push(word);
        });
        line.block.lines.push(line);
        line.page.lines.push(line);
      });
      para.page.paragraphs.push(para);
    });
  });
  return page;
};